<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="mx-auto">
        <v-card-text>
            <v-row>
                <v-col cols="12">
                    <v-btn
                      class="mx-2"
                      fab
                      dark
                      small
                      color="primary"
                      @click="propuestasAdjudicadas"
                    >
                      <v-icon dark>
                        refresh
                      </v-icon>
                    </v-btn>
                    <v-btn
                      class="mx-2"
                      dark
                      color="primary"
                      @click="control = !control,dialog_modabogado = true"
                    >
                      Modificar Abogado
                    </v-btn>
                    <v-text-field
                        v-model="search"
                        append-icon="search"
                        label="Buscar"
                        single-line
                        hide-details
                    >
                    </v-text-field>
                    <br>
                    <v-data-table
                        :headers="headers"
                        :items="dataConsulta"
                        :search="search"
                        class="elevation-1"
                    >
                       <template v-slot:item.valor="{ item }">
                        <span>
                          {{ $formatonumero(item.valor, 2, 1) }}
                        </span>
                      </template>

                      <template v-slot:item.verOferentes="{ item }">
                        <v-icon
                          class="mr-5"
                          color="primary"
                          @click="oferentePropuestaAdjudicada(item.id,item.titulo)"
                        >
                          person
                        </v-icon>
                      </template>
                      <template v-slot:item.verDatos="{ item }">
                        <v-icon
                          class="mr-5"
                          :color="getColorBtnData(item)"
                          @click="abrirDialogoDatos(item)"
                        >
                          assignment
                        </v-icon>
                      </template>
                      <!--
                      <template v-slot:item.cambAbogado="{ }">
                        <v-icon
                          class="mr-5"
                          @click="dialog_modabogado = true"
                          color="primary"
                        >
                          work
                        </v-icon>
                      </template>
                      -->
                      <template v-slot:item.vobo="{ item }">
                        <v-icon
                          class="mr-5"
                          :color="getColorBtnVoBo(item.vobojefjur)"
                          @click="abrirDialogoVoBo(item)"
                          :disabled="item.abogado === null || item.abogado === '' || item.abogado === 0"
                          v-if="item.abogado !== null && item.abogado !== '' && item.abogado !== 0"
                        >
                          work
                        </v-icon>
                      </template>
                      <template v-slot:item.minuta="{ item }">
                        <v-icon
                          class="mr-5"
                          color="primary"
                          @click="abrirMinuta (item)"
                          v-if="item.totalmin > 0"
                        >
                          assignment
                        </v-icon>
                      </template>
                      <template v-slot:item.docreqcont="{ item }">
                        <v-icon
                          class="mr-5"
                          color="primary"
                          @click="abrirDialogoDocumentos (item, 'RC')"
                          v-if="item.enviodocreq == 1"
                        >
                          assignment_turned_in
                        </v-icon>
                      </template>

                      <template v-slot:item.doctrapol="{ item }">
                        <v-icon
                          class="mr-5"
                          color="primary"
                          @click="abrirDialogoDocumentos (item, 'TP')"
                        >
                          assignment_turned_in
                        </v-icon>
                      </template>

                      <template v-slot:item.doclegcont="{ item }">
                        <v-icon
                          class="mr-5"
                          color="primary"
                          @click="abrirDialogoDocumentos (item, 'LC')"
                        >
                          assignment_turned_in
                        </v-icon>
                      </template>
                    </v-data-table>
                </v-col>
            </v-row>
        </v-card-text>
      </v-card>
    </v-flex>

    <v-dialog
      v-model="dialog_oferentes"
      persistent
      max-width="1200"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center">
          {{ `Adjudicación para el proceso '${tituloOferentes}'` }}
        </v-card-title>
        <v-card-text>
          <v-text-field
              v-model="searchOferente"
              append-icon="search"
              label="Buscar"
              single-line
              hide-details
          >
          </v-text-field>
          <br>
          <v-data-table
              :headers="headersOferente"
              :items="oferentes"
              :search="searchOferente"
              class="elevation-1"
              item-key="proponente"
          >
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="dialog_oferentes=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialog_datosadicionales"
      persistent
      max-width="800"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center">
          Datos del Proceso
        </v-card-title>
        <v-card-text>
          <v-form v-model="valid" ref="form">
            <v-row>
                <v-col cols="6">
                    <v-autocomplete
                        v-model="abogadoproceso"
                        :items="arrAbogados"
                        item-text="nombrecompleto"
                        item-value="codigo"
                        dense
                        label="Abogado del Proceso"
                        autocomplete="off"
                        :cache-items="false"
                        :readonly="true"
                    >
                    </v-autocomplete>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="6">
                    <v-autocomplete
                        v-model="supervisorproceso"
                        :items="arrAbogados"
                        item-text="nombrecompleto"
                        item-value="codigo"
                        dense
                        label="Supervisor"
                        autocomplete="off"
                        :cache-items="false"
                        :readonly="true"
                    >
                    </v-autocomplete>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="6">
                    <v-text-field
                        v-model="valorproceso"
                        label="Valor"
                        single-line
                        hide-details
                        @focus="formatoValorIn()"
                        @blur="formatoValorOut()"
                        :readonly="true"
                    >
                    </v-text-field>
                </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="secondary" dark text @click="dialog_datosadicionales=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialog_vobo"
      persistent
      max-width="900"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center">
          VoBo Proceso
        </v-card-title>
        <v-card-text>
          <v-form v-model="valid2" ref="form2">
            <v-row>
              <v-col cols="3">
                <v-select
                  :items="arrVoBo"
                  item-value="valor"
                  item-text="texto"
                  v-model="vobojefjur"
                  label="Visto Bueno"
                  :return-object="false"
                  :readonly="true"
                >
                </v-select>
              </v-col>
            </v-row>
            <v-row>
              <v-textarea
                filled
                name="input-7-4"
                label="Observación"
                v-model="voboobservacion"
                :readonly="true"
              ></v-textarea>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="secondary" dark text @click="dialog_vobo=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog_docrequerida" max-width="900px">
      <v-card>
          <v-card-title>
            <span class="headline">{{ titulodialogo }}</span>
          </v-card-title>
          <v-card-text>
              <TabvalidaDocReqContrato
                :idregistro.sync = "idregistro"
                :tipodoc.sync = "tipodoc"
                :idpropconv.sync = "idpropconv" />
          </v-card-text>
          <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog_docrequerida=false">Cancelar</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog_minuta" max-width="800px">
      <v-card>
          <v-card-title>
            <span class="headline">Minuta</span>
          </v-card-title>
          <v-card-text>
              <TabMinuta
                :idpropconv.sync = "idpropconv" />
          </v-card-text>
          <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog_minuta=false">Cancelar</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog_modabogado" max-width="1500px">
      <v-card>
          <v-card-title>
            <span class="headline">Modificar abogado contratos</span>
          </v-card-title>
          <v-card-text>
              <TabcambioAbogado :control.sync = "control"/>
          </v-card-text>
          <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog_modabogado=false">Cancelar</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { adminApp } from '@/services/api'
export default {
  name: 'contratosJefeJuridico',
  components: {
    TabvalidaDocReqContrato: () => import('./validaDocReqContrato'),
    TabMinuta: () => import('./tabMinutaJefeJuridico'),
    TabcambioAbogado: () => import('./modAbogadoContrato')
  },
  data () {
    return {
      search: '',
      tipo_evaluacion: '',
      oferentes: [],
      dialog_oferentes: false,
      dialog_datosadicionales: false,
      searchOferente: '',
      tituloOferentes: '',
      abogadoproceso: '',
      supervisorproceso: '',
      valorproceso: 0,
      arrAbogados: [],
      valid: false,
      item: '',
      dialog_vobo: false,
      readvbjuridica: true,
      vobojefjur: '',
      voboobservacion: '',
      itemvobo: '',
      valid2: false,
      dialog_docrequerida: false,
      idregistro: '',
      tipodoc: '',
      idpropconv: '',
      titulodialogo: '',
      dialog_minuta: false,
      dialog_modabogado: false,
      control: false,
      arrVoBo: [
        { valor: 'N', texto: 'NO' },
        { valor: 'S', texto: 'SI' }
      ],
      arrTipoEvaluacion: [
        { value: 'PM', text: 'Proceso Manual' },
        { value: 'AC', text: 'Algoritmo de cálculo' }
      ],
      dataConsulta: [],
      headers: [
        { text: 'Título del proceso', align: 'left', value: 'titulo', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha límite', align: 'left', value: 'fecha_cierre_format', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Tipo de proceso', align: 'left', value: 'tipo_contratacion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Estado', align: 'left', value: 'estado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Referencia', align: 'left', value: 'referencia', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Abogado', align: 'left', value: 'nombreabogado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Supervisor', align: 'left', value: 'nombresupervisor', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Valor', align: 'left', value: 'valor', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Oferentes', align: 'center', value: 'verOferentes', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Documentación requerida para la contratación', align: 'center', value: 'docreqcont', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Minuta', align: 'center', value: 'minuta', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Documentación para trámite de poliza', align: 'center', value: 'doctrapol', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Documentación legalización contractual', align: 'center', value: 'doclegcont', class: 'subtitle-1 font-weight-bold black--text' }
      ],
      headersOferente: [
        { text: 'Nombre/Razón social', align: 'left', value: 'Nombre', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Identificación', align: 'left', value: 'identificacion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha', align: 'left', value: 'fecha', class: 'subtitle-1 font-weight-bold black--text' }
      ],
      rules: {
        required: value => !!value || 'Campo requerido.',
        noDual: value => value !== this.abogadoproceso || 'El abogado debe ser diferente al Supervisor.',
        valorNumerico: value => parseFloat(this.$limpiarMoneda(value)) !== 0 || 'El valor debe ser diferente a cero (0).'
      }
    }
  },
  methods: {
    getColorBtnData (item) {
      if (item.abogado === '' || item.abogado === null) {
        return 'gray'
      } else {
        return 'primary'
      }
    },
    getColorBtnVoBo (vobo) {
      if (vobo === 'N') {
        return 'red'
      } else if (vobo === 'S') {
        return 'primary'
      } else {
        return 'gray'
      }
    },
    formatoValorIn () {
      this.valorproceso = this.$limpiarMoneda(this.valorproceso)
    },
    formatoValorOut () {
      this.valorproceso = this.$formatonumero(this.valorproceso, 2, 1)
    },
    async propuestasAdjudicadas () {
      try {
        const { data } = await adminApp.propuestasAdjudicadas()
        this.dataConsulta = data
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async oferentePropuestaAdjudicada (codigopropuesta, nombre) {
      try {
        const { data } = await adminApp.oferentePropuestaAdjudicada({ codigopropuesta: codigopropuesta })
        this.oferentes = data
        this.tituloOferentes = nombre
        this.dialog_oferentes = true
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async traeAbogados () {
      try {
        const { data } = await adminApp.personalPorArea({ area: 7 })
        if (data.data) {
          this.arrAbogados = data.data.filter(persona => persona.codigoestado === 1)
        }
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    limpiarDatos () {
      this.abogadoproceso = ''
      this.supervisorproceso = ''
      this.valorproceso = 0
    },
    abrirDialogoDatos (item) {
      this.item = item
      this.limpiarDatos()
      this.abogadoproceso = item.abogado
      this.supervisorproceso = item.supervisor
      this.valorproceso = this.$formatonumero(item.valor, 2, 1)
      this.dialog_datosadicionales = true
    },
    abrirDialogoVoBo (item) {
      this.vobojefjur = item.vobojefjur
      this.voboobservacion = item.voboobservacion
      this.itemvobo = item
      this.dialog_vobo = true
    },
    abrirDialogoDocumentos (item, tipo) {
      switch (tipo) {
        case 'RC':
          this.titulodialogo = 'Documentación requerida para la contratación'
          break
        case 'TP':
          this.titulodialogo = 'Documentación para trámite de póliza'
          break
        case 'LC':
          this.titulodialogo = 'Documentación legalización contractual'
          break
        default:
          break
      }
      this.idregistro = item.registro
      this.tipodoc = tipo
      this.idpropconv = item.id
      this.dialog_docrequerida = true
    },
    abrirMinuta (item) {
      this.idpropconv = item.id
      this.dialog_minuta = true
    }
  },
  mounted () {
    this.propuestasAdjudicadas()
    this.traeAbogados()
  }
}
</script>
